
import { Helmet } from "react-helmet";
import { useHistory } from "react-router-dom";

import React, { useState } from 'react';
import axios from 'axios';
import { setUserSession , getToken} from '../../api/Common';

function Login() {
  const [loading, setLoading] = useState(false);
  const username = useFormInput('');
  const password = useFormInput('');
  const [error, setError] = useState(null);
  let history = useHistory();
  if (getToken()){
	history.push("/")
  }
  // handle button click of login form
  const handleLogin = () => {
    setError(null);
    setLoading(true);
    
    axios.post('https://dqnhq15t16.execute-api.eu-west-3.amazonaws.com/prod/user', { "query": "0", "username": username.value, "password": password.value }).then(response => {
      if(response.data.statusCode === 200) {
        setUserSession(response.data.body);
		history.go("/dashboard")
      }else{
        setLoading(false);
        setError("Votre nom d'utilisateur ou adresse mail est incorrecte");
      }

    }).catch(error => {
      setLoading(false);
      if (error) {setError(error);
      }
      else setError("Something went wrong. Please try again later.");
    });
  }
    const css = `
        .App {
            background: url('https://calirextn.com/assets/images/loading-background.svg');
            background-position: top center;
            background-size: cover;
            background-repeat: no-repeat;
        }
    `
    return (
        <div className= "LoginForm">
            <Helmet>
                <style>{css}</style>
            </Helmet>
            <div className="auth-wrapper p-5">
                <div className="auth-inner"> 
                    <form>
                        <h3>Connexion</h3>

                        <div className="form-group">
                            <label>Adresse mail</label>
                            <input type="email" {...username} autoComplete="new-username"  className="form-control" placeholder="Enter email" />
                        </div>

                        <div className="form-group">
                            <label>Mot de passe</label>
                            <input type="password" {...password} autoComplete="new-password" className="form-control" placeholder="Enter password" />
                        </div>
						{error && <><small style={{ color: 'red' }}>{error}</small><br /></>}<br />
                        {/* <div className="form-group">
                            <div className="custom-control custom-checkbox">
                                <input type="checkbox" className="custom-control-input" id="customCheck1" />
                                <label className="custom-control-label" htmlFor="customCheck1">Remember me</label>
                            </div>
                        </div>
						*/}
                        <button type="button" className="btn btn-primary btn-block"  value={loading ? 'Loading...' : 'Login'} onClick={handleLogin} disabled={loading}>Se connecter</button>
                        {/* <p className="forgot-password text-right">
                            Forgot <a href="#">password?</a>
                        </p>  */}
                    </form>
                </div>
            </div>
        </div>
    );
}


const useFormInput = initialValue => {
	const [value, setValue] = useState(initialValue);
	const handleChange = e => {
		setValue(e.target.value);
	}
	return {
		value,
		onChange: handleChange
	}
  }
  

export default  Login;
