import React, {useState } from "react";
import PropTypes from "prop-types";
import { Modal, Button } from "react-bootstrap";
import Form from "@rjsf/core";
function PopupForm(props) {   
  const [show, setShow] = useState(false);
  const handleClose = () => {

    setShow(false);
  }
  const handleShow = () => setShow(true);

  const schema = {
    title: "Veuillez remplir le formulaire",
    type: "object",
    required: [],
    properties: props.schema
  }
  const onSubmit = ({formData}, e) => console.log("Data submitted: ",  formData);
  console.log("props.schema final")
  console.log(schema)
    return (
        <> 
          <button className="btn btn-primary btn-card btn-lg" onClick={handleShow}>{props.buttonTitle}</button>
          <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
              <Modal.Title>{props.modalTitle}</Modal.Title>
            </Modal.Header>
            <Modal.Body> 
              <Form schema={schema} formData={""} onSubmit={onSubmit} />
        </Modal.Body>
            <Modal.Footer>
              <Button variant="primary" type="submit" onClick={handleClose}>
                Sauvgarder
              </Button>
            </Modal.Footer>
          </Modal>
        </>  
    );
}
PopupForm.propTypes = {
  modalTitle: PropTypes.string,
  onSubmit: PropTypes.any,
  schema: PropTypes.array,
  buttonTitle: PropTypes.string
};

export default PopupForm;
