import Table from '../table/Table';
import  customerData  from '../data/customers.json';

function Customers() {
	return(
		<>
		<Table data={customerData["data"]} actions={customerData["actions"]}  columns={customerData["columns"]} title={customerData["title"]} crumbs_list={customerData["crumbs"]} ></Table>
		</>
	);
}

export default Customers;



