import Carousel from 'react-bootstrap/Carousel'
import './Slider.scss';
import PropTypes from "prop-types";
function Slider(props) {    
    const items = [];
    for (let item of props.slider_data.sliders) {
        const bgStyle = {
            background: " linear-gradient( rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5) ), "+item.style
        };
        const carousel_item = (
            <Carousel.Item className={item.class} style={bgStyle}>
                <Carousel.Caption>
                    <h1>{item.title}</h1>
                    <p>{item.subtitle}</p>
                </Carousel.Caption>
            </Carousel.Item>
      );
      items.push(carousel_item);
    }
    return (
        <Carousel variant="light" fade className={props.type}>
            {items}
        </Carousel>

        );
}

Slider.propTypes = {
    type: PropTypes.array,
    slider_data: PropTypes.object,
};

export default Slider;