import Table from '../table/Table';
import  customerData  from '../data/orders.json';
import  plans  from '../data/plans.json';
import {getOrderslist,getUserPlan, createPdf}  from '../../api/Common';
function Orders() {
	const test = JSON.parse(getOrderslist())
	const plan = getUserPlan()
	let list = []
	for (let i in test.Items){
		console.log(test.Items[i])
		list.push(test.Items[i])
		list[i]["price_delivery"]=plans[plan]["price"].toFixed(2)+"TND"
	}
	const createReciept = (data)=>{
		createReciept()
	}
	const schemaString = {
		client_id: {"type": "string", "title": "Numéro du client"},
		weight_g: {"type": "number", "title": "Poids en grammes"},
		price_base: {"type": "number", "title": "Prix du produit"}
	}

	return(
		<>
		<Table data={list} schema={schemaString} actions={customerData["actions"]}  columns={customerData["columns"]} title={customerData["title"]} crumbs_list={customerData["crumbs"]} ></Table>
		</>
	);
}

export default Orders;



